@import url("https://fonts.googleapis.com/css?family=Signika+Negative:300,400");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Pushster&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Rubik:ital,wght@1,300&display=swap");
html {
  font-size: 62.5%;
  box-sizing: border-box;
  overflow-x: hidden;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  overflow-x: hidden;
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
  font-weight: 900;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

#rotatingWords {
  display: inline-block;
}

.typeText {
  margin: 0;
  color: white;
}

.old {
  display: none;
}

.gsapCursor.blink {
  display: inline-block;
  animation: 1s blinkCursor step-end infinite;
}

.gsapCursor.hide {
  display: none;
}

@keyframes blinkCursor {
  from, to {
    color: transparent;
  }
  50% {
    color: white;
  }
}
.about,
.projects,
.contact {
  scroll-snap-align: center;
  scroll-snap-stop: always;
}

.menu-bar {
  background-image: linear-gradient(-90deg, #d38312, #a83279);
}

.nav-link {
  font-size: 16px;
  margin: 3px;
  color: #fff !important;
  font-weight: 500;
}

.navbar-nav {
  text-align: right;
  margin-right: 20px;
}

.menu-bar .navbar-toggler {
  padding-right: 20px;
  outline: none !important;
  border: none !important;
}

.navbar .fa {
  font-size: 26px;
  color: #fff !important;
}

.nav-link:hover {
  font-weight: 600;
  border-bottom: 1px solid #fff;
  padding-bottom: 1px;
}

.navbar-brand img {
  /*width: 150px;*/
  margin-top: 5px;
  margin-left: 20px;
}

@media only screen and (max-width: 1000px) {
  .nav-link:hover {
    border-bottom: none !important;
  }
}
.bg-foto--1 {
  background-image: url("/build/img/foto.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  height: 100vh;
  width: 100%;
}

.center {
  position: absolute;
  background-color: rgba(1, 1, 1, 0.3);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.title {
  padding: 0 !important;
  margin: 0 2.5rem !important;
  font-family: arial;
  color: white;
  font-weight: 900;
  margin: 20px;
  font-size: calc(1.415rem + 1.98vw) !important;
}
@media (min-width: 1200px) {
  .title {
    font-size: 2.9rem !important;
  }
}

.title--font {
  padding: 0 !important;
  margin: 0 1rem !important;
  font-family: "Pushster", cursive;
  font-size: calc(2.125rem + 10.5vw) !important;
}
@media (min-width: 1200px) {
  .title--font {
    font-size: 10rem !important;
  }
}

@media (min-width: 480px) {
  .title {
    font-size: calc(1.425rem + 2.1vw) !important;
  }
}
@media (min-width: 480px) and (min-width: 1200px) {
  .title {
    font-size: 3rem !important;
  }
}
@media (min-width: 480px) {
  .hello {
    font-size: calc(2.125rem + 10.5vw) !important;
  }
}
@media (min-width: 480px) and (min-width: 1200px) {
  .hello {
    font-size: 10rem !important;
  }
}
@media (min-width: 768px) {
  .title {
    font-size: calc(1.625rem + 4.5vw) !important;
  }
}
@media (min-width: 768px) and (min-width: 1200px) {
  .title {
    font-size: 5rem !important;
  }
}
@media (min-width: 768px) {
  .hello {
    font-size: calc(1.825rem + 6.9vw) !important;
  }
}
@media (min-width: 768px) and (min-width: 1200px) {
  .hello {
    font-size: 7rem !important;
  }
}
@media (min-width: 1200px) {
  .title {
    font-size: calc(1.575rem + 3.9vw) !important;
  }
}
@media (min-width: 1200px) and (min-width: 1200px) {
  .title {
    font-size: 4.5rem !important;
  }
}
@media (min-width: 1200px) {
  .hello {
    font-size: calc(1.725rem + 5.7vw) !important;
  }
}
@media (min-width: 1200px) and (min-width: 1200px) {
  .hello {
    font-size: 6rem !important;
  }
}
.bg-foto--2 {
  background-image: url("/build/img/cruz.jpg");
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 300px;
}

.text--about {
  color: #fff;
  font-size: 1.2rem;
  font-weight: 300;
  line-height: 1.5;
  margin: 0 1rem;
}

.bg--black {
  width: 100%;
  height: 100%;
  background-color: rgba(1, 1, 1, 0.3);
  padding-top: 7rem;
}

.center--top {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/*  Skills  */
.skill-item {
  margin-bottom: 1rem;
}

.skill-item h6 {
  font-size: 15px;
  font-weight: 500;
  margin-bottom: 5px;
  color: #fff;
}

.skill-item .skill-progress {
  background-color: #eee;
  border-radius: 20px;
}

.skill-item .skill-progress-bar {
  width: 0;
  height: 4px;
  position: relative;
  border-radius: 20px;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.skill-item .skill-progress-bar span {
  position: absolute;
  right: 0;
  top: -24px;
  font-size: 13px;
  font-weight: 500;
  color: #fff;
}

.section-title {
  text-align: center;
  margin-bottom: 2rem;
}

.section-title h2 {
  position: relative;
  font-weight: 700;
  padding-bottom: 1rem;
  font-size: 40px;
  line-height: 1.3;
  text-transform: capitalize;
  color: #fff;
  font-family: "Rubik", sans-serif;
}

.section-title h2:after {
  /* autoprefixer: off */
  background-image: -webkit-linear-gradient(left, rgba(0, 0, 0, 0), #a5a5a5, rgba(0, 0, 0, 0));
  background-image: -moz-linear-gradient(left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
  background-image: -ms-linear-gradient(left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
  background-image: -o-linear-gradient(left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffffff", endColorstr="#000000", GradientType=1);
  /* autoprefixer: on */
  position: absolute;
  left: 0;
  right: 0;
  height: 1px;
  width: 100%;
  content: "";
  bottom: 0px;
  max-width: 300px;
  margin: 0 auto;
}

.bg-base-color {
  background-color: #b74f53;
}

@media (min-width: 480px) {
  .skill-item {
    margin-bottom: 2.5rem;
  }

  .text--about {
    font-size: 1.7rem;
  }
}
@media (min-width: 768px) {
  .skill-item {
    margin-bottom: 4rem;
  }

  .text--about {
    font-size: 2rem;
    padding: 2rem 0;
  }
}
@media (max-height: 570px) {
  .title--font {
    font-size: calc(1.525rem + 3.3vw) !important;
  }
}
@media (max-height: 570px) and (min-width: 1200px) {
  .title--font {
    font-size: 4rem !important;
  }
}
@media (max-height: 570px) {
  .section-title h2 {
    font-size: calc(1.425rem + 2.1vw) !important;
  }
}
@media (max-height: 570px) and (min-width: 1200px) {
  .section-title h2 {
    font-size: 3rem !important;
  }
}
.bg-foto--3 {
  background-image: url("/build/img/project.jpg");
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 300px;
}

.tns-controls {
  text-align: center;
  margin-bottom: 10px;
}

.tns-controls [aria-controls] {
  font-size: 15px;
  margin: 0 5px;
  padding: 0 1em;
  height: 2.5em;
  color: #000;
  background: #66ccff;
  border-radius: 3px;
  border: 0;
}

.tns-controls [disabled] {
  color: #999999;
  background: #b3b3b3;
  cursor: not-allowed !important;
}

.tns-nav {
  text-align: center;
  margin: 10px 0;
}

.tns-nav > [aria-controls] {
  width: 9px;
  height: 9px;
  padding: 0;
  margin: 0 5px;
  border-radius: 50%;
  background: #ddd;
  border: 0;
}

.tns-nav > .tns-nav-active {
  background: #999;
}

.img-card {
  display: block;
  width: 100%;
  height: 100%;
  margin-bottom: 0;
}

.card--footer {
  display: flex;
  width: 100%;
  height: 20%;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  margin-top: 0;
}
.card--footer .card--icons a i {
  font-size: 2rem;
}
.card--footer .card--icons .card--icon:hover {
  cursor: pointer;
  color: #6ef5fa;
}
.card--footer .card--icons .card--icon:active {
  color: #15a3a8;
}

.bg-foto--4 {
  background-image: url("/build/img/contact.jpg");
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 300px;
}

.form-control {
  height: 52px;
  background: transparent;
  color: rgba(255, 255, 255, 0.8) !important;
  font-size: 14px;
  border-radius: 0px;
  -webkit-box-shadow: none !important;
  box-shadow: none !important;
  border: 1px solid rgba(255, 255, 255, 0.2);
}
.form-control::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: rgba(255, 255, 255, 0.5) !important;
}
.form-control::-moz-placeholder {
  /* Firefox 19+ */
  color: rgba(255, 255, 255, 0.5) !important;
}
.form-control:-ms-input-placeholder {
  /* IE 0+ */
  color: rgba(255, 255, 255, 0.5) !important;
}
.form-control:-moz-placeholder {
  /* Firefox 18- */
  color: rgba(255, 255, 255, 0.5) !important;
}
.form-control:focus, .form-control:active {
  border-color: #01d28e !important;
}

textarea.form-control {
  height: inherit !important;
}

.form-control:focus,
.form-control:active {
  background: transparent;
  border-color: rgba(255, 255, 255, 0.5) !important;
}

.contactForm .label {
  color: #fff;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 600;
}

.contactForm .form-control {
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding: 0;
}

#contactForm .error {
  color: red;
  font-size: 12px;
}

#contactForm .form-control {
  font-size: 16px;
}

#message {
  resize: vertical;
}

#form-message-warning,
#form-message-success {
  display: none;
}

#form-message-warning {
  color: red;
}

#form-message-success {
  color: #28a745;
  font-size: 18px;
  font-weight: bold;
}

.submitting {
  float: left;
  width: 100%;
  padding: 10px 0;
  display: none;
  font-size: 16px;
  font-weight: bold;
}

.btn {
  padding: 12px 16px;
  cursor: pointer;
  border-width: 1px;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 400;
  -webkit-box-shadow: 0px 10px 20px -6px rgba(0, 0, 0, 0.12);
  -moz-box-shadow: 0px 10px 20px -6px rgba(0, 0, 0, 0.12);
  box-shadow: 0px 10px 20px -6px rgba(0, 0, 0, 0.12);
  position: relative;
  margin-bottom: 20px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

@media (prefers-reduced-motion: reduce) {
  .btn {
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
  }
}
.btn:hover,
.btn:active,
.btn:focus {
  outline: none !important;
  -webkit-box-shadow: 0px 10px 20px -6px rgba(0, 0, 0, 0.22) !important;
  -moz-box-shadow: 0px 10px 20px -6px rgba(0, 0, 0, 0.22) !important;
  box-shadow: 0px 10px 20px -6px rgba(0, 0, 0, 0.22) !important;
}

.btn.btn-primary {
  background: #01d28e !important;
  border-color: #01d28e !important;
  color: #fff;
}

.btn.btn-primary:hover,
.btn.btn-primary:focus {
  border-color: #019f6c !important;
  background: #019f6c !important;
}

.align-right {
  justify-content: flex-end;
}

.bold {
  font-weight: 500;
}

.white-text {
  color: #fff;
}

.social div i {
  color: #ff9c17;
  -webkit-transition: color 0.2s ease-in-out;
  transition: color 0.2s ease-in-out;
  width: 2.2rem;
  height: 2.2rem;
  -webkit-transform: scale(1);
  transform: scale(1);
  -webkit-transition: -webkit-transform 0.2s ease-in-out;
  transition: -webkit-transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}

.social div i:hover {
  color: #f0535d;
  background-color: transparent;
  -webkit-transform: scale(1.4);
  transform: scale(1.4);
}

.header {
  width: 100%;
  box-shadow: 0 0.3rem 0.4rem #444;
  background-image: linear-gradient(-90deg, #d38312, #a83279);
  position: fixed;
  z-index: 99;
}

#pagescroll {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.section {
  height: 100vh;
  width: 100%;
  transition: transform 0.3s ease-out;
}

.controllers {
  z-index: 999;
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  right: 20px;
  display: flex;
  flex-direction: column;
}

#control-up,
#control-down {
  cursor: pointer;
  margin: 5px;
  transform: scale(0.5);
  transition: 0.2s;
}

#control-down {
  transform: rotate(180deg) scale(0.5);
}

#control-down:hover {
  transform: rotate(180deg) scale(0.7);
}

#control-up:hover {
  transform: scale(0.7);
}